.device-status {
  background: white; }
  .device-status .module__content {
    display: flex;
    width: 100%;
    padding: 0.52083vw 0; }
  .device-status__device {
    flex: 1 1 16.6%;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .device-status__device.active .device-status__light {
      fill: #5ed60e; }
    .device-status__device.blocked .device-status__light {
      fill: #C94E5D; }
    .device-status__device.disconnected .device-status__light {
      fill: #c9c9c9; }
  .device-status__light {
    width: 5.20833vw;
    height: 5.20833vw;
    margin-bottom: 2.60417vw; }
  .device-status__battery {
    width: 5.20833vw;
    height: 5.20833vw;
    transform: rotate(90deg); }
  .device-status .device__title {
    font-size: 2.08333vw;
    line-height: 2.60417vw;
    height: 2.60417vw;
    margin-bottom: 2.60417vw; }
